@import '../../../../../../../../../constants.scss';

.immovable {

  &__fields {

    & > div {
      margin-bottom: 25px;
    }

    .general {

      &__type {

        &-title {
          margin-bottom: 10px;
          font-family: 'Montserrat', sans-serif;
          font-size: 12px;
          line-height: 12px;
          letter-spacing: 0.4px;
          color: #263238;
        }
      }

      .duet {
        display: flex;

        & > div:first-child {
          margin-right: 20px;
        }
      }

      .trio {
        display: flex;

        & > div:first-child, & > div:nth-child(2) {
          margin-right: 20px;
        }
      }
    }

    .security-payment {

      &__currency {

        &-title {
          margin-bottom: 10px;
          font-family: 'Montserrat', sans-serif;
          font-size: 12px;
          line-height: 12px;
          letter-spacing: 0.4px;
          color: #263238;
        }
      }
    }

    .templates {
      
      .single {
        width: 330px;
      }
    }
  }
}